.card {
  box-shadow: 0px 1px 16px 0px rgba(0, 0, 0, 0.1);
  width: 272px;
  height: 320px;
  border-radius: 8px;
  background-color: var(--bgr-color);
  transition: transform 0.3s ease-in;
  transform: scale(1);
  cursor: pointer;

  &:hover {
    transform: scale(1.1);
  }

  &__content {
    display: flex;
    flex-direction: column;
    padding: 0 10px;
    width: 260px;
  }

  &__title {
    color: var(--text-color);
    font-size: 14px;
    font-weight: 800;
    line-height: 21px;
    padding: 15px 0 15px 0;
  }

  &__details {
    display: flex;
    flex-direction: column;

    &--population,
    &--region,
    &--capital {
      display: flex;
      justify-content: left;
      padding-bottom: 8px;
      gap: 4px;
    }
  }

  .card__image {
    width: 100%;
    height: 180px;

    & img {
      width: 100%;
      height: 100%;
      border-radius: 8px 8px 0 0;
      object-fit: cover;
    }
  }

  .detail__title {
    color: var(--text-color);
    font-size: 12px;
    font-weight: 600;
  }

  .detail__value {
    color: var(--input-text);
    font-size: 12px;
    font-weight: 400;
  }
}
